.square {
    width: 100px;
    height: 100px;
    border: 2px solid #000000;
    border-radius: 5px;
    display: grid;
    place-items: center;
    cursor: pointer;
}

.square span {
    display: block;
    position: relative;
    width: 70%;
    height: 70%;
    border-radius: 50%;
    border: 8px solid transparent;
}

.square span.x::after,
.square span.x::before {
    position: absolute;
    content: "";
    width: 150%;
    height: 8px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
    background: #fc3434;
    border-radius: 5px;
}

.square span.x::after {
    transform: translate(-50%, -50%) rotate(45deg);
    opacity: 1;
}

.square span.o {
    border-color: #ff8c00;
}
